<template>
    <div class="Login">
        <Header>
            <i slot="first" class="iconfont icon-prev" @click="$router.go(-1)"></i>
            <p slot="mid">密码登录</p>
            <div slot="last"></div>
        </Header>
        <div class="PasswordLogin">
            <!-- 账号 -->
            <p><input type="text" v-model="username" placeholder="账号"></p>
            <!-- 密码 -->
            <p><input :type="checked?'text':'password'" v-model="password" placeholder="密码"> <van-switch v-model="checked" size="0.4rem"/></p>
            <!-- 验证码 -->
            <p>
                <input type="text" v-model="verificationCode" placeholder="验证码">
                <img :src=code>
            <!-- 验证码换码 -->
                <ul @click="Huan()">
                    <li>看不清</li>
                    <li>换一张</li>
                </ul>
            </p>
                
        </div>
            <!-- 温馨提示 -->
        <div class="WarmPrompt">
            <div><strong>温馨提示：未注册过的账号，登录时将自动注册</strong>
            <strong>注册过的用户可凭账号密码登录</strong></div>
        </div>
        <!-- 登录按钮 -->
        <div class="LoginButton">
            <input type="button" value="登录" @click="deng()">
        </div>
        <div class="ResetPasswords">
            重置密码？
        </div>
    </div>
</template>
<script>
import Header from "@/page/City/Header"
axios.defaults.withCredentials=true;
import axios from "axios"
export default {
    components:{
        Header
    },
   data() {
      return {
          checked: false,
          username:"",
          password:"",
          verificationCode:"",
          code:"",
      }
   },
   created() {
   },
   mounted() {
       this.Huan()
   },
   methods: {
       Huan(){
            axios.post('http://elm.cangdu.org/v1/captchas',{}).then((res)=>{
                if(res.data.code!=""){
                    this.code=res.data.code
                }
            })
       },
       deng(){
           axios.post('http://elm.cangdu.org/v2/login',{captcha_code:this.verificationCode,password:this.password,username:this.username}).then((res)=>{
            //    console.log(res)
               if(res.data.id){
                //    localStorage.setItem('Code',JSON.stringify(res.data))
                this.$store.commit("fang",res.data)
                   this.$router.push('/home/takeout')
               }else{
                   alert(res.data.message)
               }
           })
       }
   }
}
</script>

<style lang="scss">
    .Login{
        width: 100%;
        background: #F5F6F5;
        .PasswordLogin{
            background: white;
            margin-top: 1rem;
            >p{
                font-size: 0.3rem;
                line-height: 0.65rem;
                border-bottom:1px solid rgb(226, 219, 219); 
                input{
                    width: 100%;
                    padding: 0.1rem 0.2rem;
                    height: 0.5rem;
                    border: none;
                }
            }
            >p:nth-of-type(2){
                font-size: 0.3rem;
                input{
                    width:80%;
                }
                .van-switch{
                    float: right;
                    margin-top: 0.05rem;
                    margin-right: 0.4rem;
                }
            }
            >p:nth-of-type(3){
                display: flex;
                font-size: 0.3rem;
                img{
                    margin-left:2.5rem;
                    height: 0.5rem;
                }
                input{
                    width:30%;
                }
                ul{
                    li:nth-of-type(2){
                        color: #3399ff;
                        height: 0.5rem;
                    }
                     li:nth-of-type(1){
                        // color: #3399ff;
                        height: 0.3rem;
                    }
                }
            }
        }
        .right{
            width: 100%;
            display: flex;
            justify-content: space-between;
            .van-switch{
                margin-top: 0.1rem;
            }
        }
        .WarmPrompt{
            width: 100%;
            height: 0.5rem;
        }
        .WarmPrompt>div>strong{
            font-size: 0.25rem;
            display: flex;
            flex-wrap: wrap;
            margin-left: 0.2rem;
            color: #E22018;
            line-height: 0.45rem;
        }
        .LoginButton{
            width: 100%;
            text-align: center;
            margin-top: 0.5rem;
        }
        .LoginButton>input{
            width: 90%;
            height: 0.5rem;
            font-size: 0.3rem;
            background: #86DB47;
            color: white;
            border: none;
            border-radius: 0.05rem;
        }        
        .ResetPasswords{
            font-size: 0.2rem;
            margin-left:6rem;
            color: #2472C8;
        }
    }
</style>


